import { Component, OnInit } from '@angular/core';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanAdressComponent } from 'src/app/components/africlean-adress/africlean-adress.component';
import { AfricleanHttpUserProvider } from 'src/providers/africlean-http/africlean-http-user.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanObservablesProvider } from 'src/providers/africlean-observables/africlean-observables.provider';

@Component({
  selector: 'app-informations',
  templateUrl: './informations.page.html',
  styleUrls: ['./informations.page.scss'],
})
export class InformationsPage implements OnInit {

  /**
   * Contrôle l'accès aux informations
   */
  public informationsCheck = false;

  /**
   * Contrôle l'accès au mot de passe
   */
  public pwdCheck = false;

  /**
   * Contrôle l'accès aux adresses
   */
  public adressCheck = false;

  /**
   * Liste des adresses de l'utilisateurs
   */
  public adresses: any[];

  constructor(private modal: AfricleanModalProvider,
              private userProvider: AfricleanHttpUserProvider,
              private session: AfricleanSessionProvider,
              private observableProvider: AfricleanObservablesProvider) { }

  ngOnInit() {
    this.getAdresses();
    // Dés qu'il y'a un chamgement au nivea des adresses notament une mise a jour,
    // on récharge les données.
    this.observableProvider.loadAdresses.subscribe(
      (load) => {
          if (load) {
            this.getAdresses();
          }
      }
    );
  }

  /**
   * Switch vers les informations
   * @param event evenement
   */
  public toggleInformations(event: any) {
    this.informationsCheck = event.detail.checked;
  }

  /**
   * Switch vers le mot de passe
   * @param event événement
   */
  public togglePassword(event: any) {
    this.pwdCheck = event.detail.checked;
  }

  /**
   * Switch vers l'adresse
   * @param event événement
   */
  public toggleAdress(event: any) {
    this.adressCheck = event.detail.checked;
  }

  /**
   * Ajoute une nouvelle adresse
   */
  public async addAdress() {
    const props = {
      displayButton: true
    };
    await this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css', props);
    const data = await this.modal.getData();
    // tslint:disable-next-line: no-unused-expression
    !this.adresses ? this.adresses = [] : '';
    // tslint:disable-next-line: no-unused-expression
    data ? this.adresses.push(data) : '';
  }

  /**
   * Actualise la liste des adresses
   * @param event evenement émis
   */
  public actualizeAdress(homeCode: any) {
      const adressesTMP = [];
      this.adresses.forEach(adress => {
        if (adress.idHome !== homeCode) {
          adressesTMP.push(adress);
        }
      });
      this.adresses = adressesTMP;
  }

  /**
   * Renvoie toutes les adresses
   */
  public async getAdresses() {
      const userId = await this.session.getItem('userId');
      const userRole = await this.session.getItem('role');
      const url = 'user/adress/' + userId;
      const body = {
        role: userRole
      };
      this.userProvider.getAdress(url, body).subscribe(
        (adresses) => {
            this.adresses = adresses;
        }
      );
  }
}
